import Info from './Info';
import { Observable } from './utils';
import cities from './city.list.json';

export default class Forecast extends Observable {
  constructor() {
    super();
    this.info = null;
    this.update = null;
    this.cities = cities;
    this.tempUnit = 'celsius';
  }

  get(id) {
    if (id) return this.byId(id);
    return this.byLocation();
  }

  async byId(id) {
    const { info } = this;
    if (info === null || info.city.id !== id) {
      clearInterval(this.update);
      return fetch(
        `https://api.openweathermap.org/data/2.5/forecast?id=${id}&cnt=6&appid=f61acd58d6e808934570a8fec6f4c8a7`,
        { mode: 'cors' }
      )
        .then(response => {
          if (response.ok) return response;
          throw new Error('Something went wrong');
        })
        .then(response => response.json())
        .then(data => {
          this.info = Info(data);
          this.update = setInterval(() => this.byId(id), 600000);
          return this.info;
        });
    }
    return this.info;
  }

  byLocation() {
    const { info } = this;
    return new Promise((resolve, reject) => {
      const success = async position => {
        const { latitude: lat } = position.coords;
        const { longitude: lon } = position.coords;
        if (
          info === null ||
          info.city.coord.lat !== lat ||
          info.city.coord.lon !== lon
        ) {
          clearInterval(this.update);
          resolve(
            fetch(
              `https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&cnt=6&appid=f61acd58d6e808934570a8fec6f4c8a7`,
              { mode: 'cors' }
            )
              .then(response => {
                if (response.ok) return response;
                throw new Error('Something went wrong');
              })
              .then(response => response.json())
              .then(data => {
                this.info = Info(data);
                this.update = setInterval(() => this.byLocation(), 600000);
                return this.info;
              })
          );
        }
      };

      const error = () => {
        this.info = 'Unable to retrieve your location';
        reject(new Error(this.info));
      };

      if (!navigator.geolocation) {
        this.info = 'Geolocation is not supported by your browser';
        reject(new Error(this.info));
      } else {
        navigator.geolocation.getCurrentPosition(success, error);
      }
    });
  }

  updateTime() {
    const now = new Date();
    this.info.fore[0].date = now;
    return now;
  }

  getCities() {
    return this.cities;
  }

  getInfo() {
    return this.info;
  }

  showIn(tempUnit) {
    this.tempUnit = tempUnit.toLowerCase();
    return this.tempUnit;
  }
}
